@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (min-width: 1200px) {
    .container {
        max-width: 1250px;
    }
}

nav {
    z-index: 9999;
    padding-top: 27px !important;
    font-family: 'Nunito';
}

nav .get-started {
    font-weight: bold;
    color: #F9B623;
    padding: 16px 32px;
    border: solid 1px #F9B623;
    border-radius: 10px;
    transition: .3s;
}

nav .get-started:hover {
    background: #F9B623;
    transition: .3s;
    color: white;
}

nav .active .nav-link {
    color: #F9B623 !important;
    font-weight: bold;
}

nav .hvr {
    font-weight: bold;
    color: rgba(0, 0, 0, 0.75) !important;
}

/* nav .hvr .nav-link::after {} */

nav .hvr .nav-link:hover::after {
    content: '';
    display: block;
    border-bottom: #F9B623 2px solid;
    width: 50%;
    margin: auto;
    padding-top: 5px;
    margin-bottom: -7px;
}

nav .hvr .nav-link:hover {
    color: #F9B623 !important;
}

nav .nav-item {
    margin-right: 40px;
}

@media screen and (max-width: 991px) {
    nav {
        background: white;
    }

    nav .hvr .nav-link:hover::after {
        content: '';
        display: none;
        border-bottom: #F9B623 2px solid;
        width: 50%;
        margin: auto;
        padding-top: 5px;
        margin-bottom: -7px;
    }

    nav .get-started {
        font-weight: bold;
        color: #F9B623;
        padding: 0;
        border: none;
        border-radius: 10px;
        transition: .3s;
    }

    nav .get-started:hover {
        background: none;
        transition: .3s;
        color: #F9B623;
    }
}
